#show-small {
  z-index: 1;
}

#small-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: brown;
  width: 100%;
}

@media (max-width: 768px) {
  #contai {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-top: 0.6rem;
  }

  #inv-logo {
    margin-top: 1.2rem;
    margin-bottom: 0.5rem;
    text-align: center;
  }

  #inv-nav {
    margin-left: 1rem;
    margin-right: 1rem;
  }
}

@media screen and (width <= 575px) {
  .first-view-main {
    margin-top: 42rem;
  }

  .l-prm-layout--center .article-intro.article-container:before {
    /* background-image: linear-gradient(#fff 30px, #323a56 30px); */
    background-color: #323a56;
  }

  .l-prm-layout--center .article-intro:before {
    content: "";
    z-index: -1;
    width: 100vw;
    height: 100%;
    /* display: flex; */
    display: block;
    position: absolute;
    left: calc(50% - 50vw);
  }

  /* .l-prm-layout--center .article-intro.article-container,
    .l-prm-layout--center .product-disclosure,
    .l-prm-layout--left .article-intro.article-container,
    .l-prm-layout--left .product-disclosure {
      margin: 0 auto;
      padding: 0 0 1rem;
      max-width: 100% !important;
    } */

  #article-intro_1-0 {
    position: sticky;
  }
}

/* Styles for desktop devices with a screen width of 768 pixels or more */
@media (min-width: 768px) {
  #mydesktopcntnt {
    display: block;
  }

  #myTabContent {
    display: none;
  }
}

/* Styles for mobile devices with a screen width less than 768 pixels */
@media (max-width: 767px) {
  html {
    background-color: rgba(33, 37, 41) !important;
  }

  body {
    background-color: rgba(33, 37, 41) !important;
  }

  #mydesktopcntnt {
    display: none;
  }

  #myTabContent {
    display: block;
  }
}

@font-face {
  font-family: "Centima"; /* Give your font a name */
  src: url("./centima.ttf") format("truetype"); /* Specify the path to your TTF file */
  font-weight: normal;
  font-style: normal;
}
